.topN{
    width: 100%;
    height: 60px;
    background-color: white;
    display: flex;
    justify-content: center;
    position: fixed;
    align-items: center;
    z-index: 999;
    box-shadow: 0px 0px 4px #000000a8;
}
.topN h1{
    font-size: 20px;
    text-align: center;
    font-weight: 500;
}
.topN .button1 , .topN .button2{
    width: 100px;
    height: 40px;
    position: absolute;
    font-size: 15px;
    font-weight: 200;
    margin: 10px;
    color: white;
    right: 120px;
    border: 0.4px solid #bdbdbd;
    background-color: green;
    border-radius: 10px;
    box-shadow: 0px 3px 6px 2px #c7c7c7;
    cursor: pointer;
    transition: 0.2s;
}
.topN .button1:hover , .topN .button2:hover{
    transform: scale(1.03);
}
.topN .button2 {
    right: 0;
    background-color: transparent;
    color: black;
}

@media screen and (max-width: 580px) {
    .topN{
        justify-content: start;
    }
    .topN .button1 , .topN .button2{
        width: 70px;
        height: 30px;
        margin: 5px;
        font-size: 11px;
        right: 75px;
    }
    .topN .button2 {
        right: 0;
    }
    .topN h1{
        margin-left: 5px;
    }
}