.wel{
    font-size: 50px;
    margin: 0;
    padding: 30px;
    background-color: black;
    color: white;
    font-weight: 200;
    text-transform: capitalize;
}
.wel span{
    font-weight: 500;
}
.cont{
    display: flex;
    width: 100%;
    padding-left:260px;
    /* justify-content: center; */
    text-align: center;
    flex-direction: column;
}
.clist{
    flex-wrap: wrap;
    height: 80vh;
    overflow: scroll;
    justify-content: center;
}
@media screen and (max-width: 889px) {
    .cont{
        padding: 0;
    }
    .wel{
        padding: 20px;
        font-size: 30px;
    }
    
}