.cardCont {
    margin: 20px;
    flex-direction: row;
    width: 270px;
    min-height: 300px;
    box-shadow: 2px 3px 5px 1px rgba(0, 0, 0, 0.5);
    border-radius: 50px;
    transition: 300ms ease-out;
    
}

.cardCont h1 {
    width: 100%;
    justify-content: center;
    align-items: center;
    border-top-left-radius:50px ;
    border-top-right-radius:50px ;
    height: 190px;
    font-size: 40px;
    font-weight: 400;
    margin: 0;
    box-shadow: inset 0px 0px 0px 20px rgb(255 255 255 / 50%);
    background-color: #003b3d56;
}
.cardCont:hover{
    transform: scale(1.05);
    box-shadow: 4px 6px 9px 3px rgba(0, 0, 0, 0.4);
}